import React from "react";
import { css } from "@emotion/css";

interface Props {
  data: {
    question: string;
    answer1: string;
    answer1Code: string;
    answer2: string;
    answer2Code: string;
    imgUrl: string;
  };
  results: {
    I: number;
    E: number;
    N: number;
    S: number;
    T: number;
    F: number;
    P: number;
    J: number;
  };
  isMobile?: boolean;
  _handleClick: (type?: string) => void;
}

const Questions = ({ data, _handleClick, isMobile = false }: Props) => {
  console.log("question", isMobile);
  return (
    <div className={isMobile ? styles.question_wrapper : styles.m_wrapper}>
      <img
        src={data.imgUrl}
        alt=""
        className={isMobile ? styles.img : styles.m_img}
      />
      <div
        className={isMobile ? styles.question : styles.m_question}
        style={{ opacity: 1 }}
      >
        {data.question}
      </div>
      <button
        className={isMobile ? styles.btn : styles.m_btn}
        onClick={() => _handleClick(data.answer1Code)}
      >
        <span className={isMobile ? styles.t : styles.mt}>{data.answer1}</span>
      </button>
      <button
        className={isMobile ? styles.btn : styles.m_btn}
        onClick={() => _handleClick(data.answer2Code)}
      >
        <span className={isMobile ? styles.t : styles.mt}>{data.answer2}</span>
      </button>
    </div>
  );
};

export default Questions;

const styles = {
  question_wrapper: css`
    width: 40vw;
    font-size: 18px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    padding: 12vh 5vw;
  `,
  m_wrapper: css`
    width: 80vw;
    font-size: 6vw;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    padding: 2vh 5vw;
  `,
  img: css`
    height: 40vh;
    width: 30vw;
    opacity: 0.2;
  `,
  m_img: css`
    width: 80vw;
    height: 40vh;
    opacity: 0.2;
  `,
  question: css`
    padding: 0 4vw;
    top: 20vh;
    font-family: "Noto Sans KR", sans-serif;
    font-weight: 900;
    font-size: 1vw;
    object-fit: cover;
    background-repeat: no-repeat;
    font-weight: 700;
    position: absolute;
    width: 30vw;
  `,
  m_question: css`
    padding: 0 4vw;
    top: 15vh;
    font-family: "Noto Sans KR", sans-serif;
    font-weight: 900;
    font-size: 4vw;
    object-fit: cover;
    background-repeat: no-repeat;
    font-weight: 700;
    position: absolute;
    width: 65vw;
  `,
  btn: css`
    background-color: #ffffff;
    border: 0.5px solid #000;
    cursor: pointer;
    border: none;
    height: 20vh;
    width: 35vw;

    margin-top: 6vh;
    border-radius: 16px;
  `,
  m_btn: css`
    background-color: #ffffff;
    border: 0.5px solid #000;
    cursor: pointer;
    border: none;
    height: 20vh;
    width: 60vw;

    margin-top: 3vh;
    border-radius: 16px;
  `,
  t: css``,
  mt: css`
    font-size: 4vw;
    font-weight: 700;
  `,
};
