import React, { useState } from "react";
import Questions from "../components/Question";
import Result from "../components/Result";
import { items } from "../utils/constant";
import { css } from "@emotion/css";
import "./Home.scss";
import { useEffect } from "react";
const results = { I: 0, E: 0, N: 0, S: 0, T: 0, F: 0, P: 0, J: 0 };

const Home = () => {
  const [index, setIndex] = useState(0);
  const [isMobile, setMobile] = useState(true);
  console.log("main", isMobile);

  useEffect(() => {
    if (typeof window !== "undefined" && window.innerWidth >= 768) {
      console.log("false");
      console.log(window.innerWidth);
      setMobile(false);
    }
  }, []);
  const _handleClick = (type?: string) => {
    setIndex(index + 1);
    if (type) {
      results[type] += 1;
    }
  };
  return (
    <div className={styles.wrapper}>
      {index === 0 ? (
        <div className={isMobile ? styles.mobile_container : styles.container}>
          <img
            src="https://raw.githubusercontent.com/leeyh-kor/dronehi/master/src/components/images/Q4.jpg"
            alt=""
            className={isMobile ? styles.img : styles.mobile_img}
          ></img>

          <h3 className={isMobile ? styles.title : styles.mobile_title}>
            나와 닮은 역사 인물 테스트
          </h3>

          <button
            onClick={() => _handleClick()}
            className={isMobile ? styles.btn : styles.mobile_btn}
          >
            <p className={isMobile ? styles.p : styles.mp}> 과거로 돌아가기</p>
          </button>
        </div>
      ) : index === items.length + 1 ? (
        <Result results={results} isMobile={isMobile} />
      ) : (
        <Questions
          data={items[index - 1]}
          results={results}
          isMobile={isMobile}
          _handleClick={_handleClick}
        />
      )}
    </div>
  );
};
export default Home;

const styles = {
  wrapper: css`
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  `,
  container: css`
    width: 50%;
    display: flex;
    gap: 20px;
    text-align: center;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  `,
  mobile_container: css`
    width: 100%;
    display: flex;
    gap: 20px;
    text-align: center;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  `,
  img: css`
    height: 30vh;
    margin-top: 12vh;
  `,
  mobile_img: css`
    width: 80vw;
    margin-top: 20vh;
  `,
  title: css`
    width: 100%;
    height: 5vh;
    line-height: 5vh;
    vertical-align: center;
    text-align: center;
    font-size: 18px;
    font-weight: 700;
  `,
  mobile_title: css`
    width: 70vw;
    height: 5vh;
    line-height: 5vh;
    vertical-align: center;
    text-align: center;
    font-size: 5vw;
    font-weight: 700;
    margin-top: 6vh;
  `,
  btn: css`
    background-color: #e4bc58;
    border: none;
    font-weight: 700;
  `,
  mobile_btn: css`
    background-color: #e4bc58;
    border: none;
    font-weight: 700;
    width: 70vw;
    margin-top: 4vh;
  `,
  p: css`
    font-size: 18px;
  `,
  mp: css`
    font-size: 7vw;
  `,
};
